.Dialogs {
  display: grid;
  grid-template-columns: 4fr 10fr;
  gap: 10px;
  box-shadow: 0px 0px 1px 0px black;
  border-radius: 4px;
  /* background-color: #aed6f7; */
  height: 500px;
  overflow: hidden;
}

.messagelist {
  border-radius: 3px;
}

.itemD::-webkit-scrollbar {
  background-color: rgb(170, 170, 253);
  width: 5px;
}

.itemD::-webkit-scrollbar-thumb {
  background-color: rgb(65, 65, 128);
}

.itemD {
  overflow-y: scroll;
  max-height: 430px;
}

.messagelist img {
  width: 60px;
  height: 60px;
  border-radius: 30px;
  float: right;
}

.dialogMessage {
  margin-top: 0px;
  border-radius: 10px;
  width: 600px;
}

/* .dialog_container {
  width: 200px;
  outline: hidden;
  word-wrap: break-word;
} */

.dialogMessage .img img {
  margin-left: 10px;
  width: 15px;
  cursor: pointer;
}

.dialogMessage .img {
  opacity: 0.2;
  float: right;
}

.dialogMessage .img:hover {
  opacity: 0.8;
}

.dialogMessage .img:active {
  opacity: 1;
}

.dialogMessage div {
  padding: 12px;
  letter-spacing: 1px;
  border-radius: 8px;
  border: solid 1px rgb(0, 0, 0, 0);
}

.dialogMessage div:hover {
  border: solid 1px rgb(0, 0, 0, 0.2);
}

.dialog {
  background-color: white;
  border-radius: 3px;
  position: relative;
}

.Dialogs .item {
  padding: 4px;
  overflow: hidden;
}

.item .names {
  float: left;
  margin-left: 20px;
}

.active .dialogStyles {
  background: rgb(227, 242, 255);
  color: black;
  box-shadow: 5px 0px 2px 0px rgb(120, 192, 250, 0.5);
  opacity: 1;
}

.dialogStyles {
  opacity: 0.5;
}

.dialog2 {
  bottom: 0;
  width: 100%;
  position: absolute;
  overflow: hidden;
}

.messagelist {
  color: rgba(28, 51, 70, 0.6);
}

.messagelist a:active {
  color: black;
}

.textareaButton {
  display: flex;
  padding: 7px;
  background-color: rgb(120, 192, 250, 0.3);
  border-radius: 4px;
  margin-top: 20px;
}

.textareaButton button {
  margin-left: 15px;
  padding: 3px;
  margin-top: 1px;
  margin-bottom: 5px;
}

.Dialogs .showMessage,
.lastmessage {
  margin-left: 20px;
  text-align: center;
  display: inline-block;
  color: rgb(85, 85, 138);
  cursor: pointer;
}

.dialogMessage .span {
  background-color: #f7f7c7;
  padding: 5px 10px 5px 10px;
  border-radius: 10px;
  color: black;
  display: inline-block;
  max-width: 370px;
}

.dialogMessage .span img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.mymessage {
  background-color: rgb(217, 217, 255);
  padding: 5px 10px 5px 10px;
  border-radius: 10px;
  outline: hidden;
  word-wrap: break-word;
  max-width: 370px;
  display: inline-block;
}

.mymessage img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.dialogStyles {
  overflow: hidden;
  padding: 4px;
}

.dialogStyles .lastmessage {
  color: rgb(0, 0, 0, 0.3);
}
